body {
  text-align: center;
  background: #000;
  margin: auto;
  padding: 0;
  overflow: hidden;
}

.slider {
  -webkit-appearance: none;
  background-color: #0000;
}

.slider:focus {
  outline: none;
}

.slider::-webkit-slider-runnable-track {
  height: 3px;
  -webkit-appearance: none;
  cursor: pointer;
  background: #000;
}

.slider::-webkit-slider-thumb {
  width: 15px;
  height: 15px;
  cursor: pointer;
  -webkit-appearance: none;
  background: #fff;
  border-radius: 8px;
  margin-top: -6px;
}

